// =============================================================================
// Manage subscription - Invoice
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-invoices {
    margin-bottom: spacer(4);

    &-actions {
      margin-top: spacer(-2);
      margin-bottom: spacer(4);
    }
  }

  &-invoice {
    display: flex;
    align-items: center;
    padding: spacer(1) 0;
    border-top: 1px solid color('x11-gray');

    &:last-of-type {
      border-bottom: 1px solid color('x11-gray');
    }

    &-icon {
      font-size: 24px;
      color: #{color('silver')};
      margin-right: spacer(2);
    }

    &-name {
      color: #{color('gun-metal')};
      font-size: 15px;
      font-weight: bold;
      margin-top: spacer(0.5);
    }

    &-date {
      font-weight: normal;
    }

    &-meta {
      color: #{color('gun-metal')};
      font-size: 13px;
    }

    &-actions {
      display: flex;
      margin-top: spacer(0.5);

      #{$root}-btn {
        line-height: 24px;
        margin-right: spacer(2);
      }
    }
  }
}
