// =============================================================================
// Manage subscription - Subscription
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-subscriptions {
    margin-bottom: spacer(4);

    #{$root}-form-check {
      width: 100%;
      margin-bottom: 0;
    }

    #{$root}-form-check-label {
      display: flex;
      width: 100%;
      align-items: center;
      padding: spacer(1) 0 spacer(1) spacer(4);
      margin-bottom: 0;

      &--no-input {
        padding-left: 0;
      }

      &:before {
        top: 50%;
        margin-top: spacer(-1);
      }

      #{$root}-subscription-created {
        text-align: right;
        margin-left: auto;
      }

      @include bp-down(xs) {
        align-items: baseline;
        flex-direction: column;

        #{$root}-subscription-created {
          text-align: left;
          margin-left: 0;

          br {
            content: ' ';

            &:after {
              content: ' '
            }
          }
        }
      }
    }
  }

  &-subscription {
    display: flex;
    align-items: center;
    border-top: 1px solid color('x11-gray');

    &--update-quantity {
      display: block;
      padding-top: spacer(3);

      #{$root}-subscription-name {
        color: color('gun-metal');
        margin-bottom: spacer(3);
      }
    }

    &:last-of-type {
      border-bottom: 1px solid color('x11-gray');
      margin-bottom: spacer(1);
    }
  }

  &-subscription-data {
    padding-right: spacer(7);
  }

  &-subscription-name {
    display: block;
    font-size: 15px;
    font-weight: bold;
    margin-bottom: spacer(0.5);
  }

  &-subscription-status {
    &--active {
      color: color('green');
    }

    &--trialing {
      color: color('tangerine');
    }
  }

  &-subscription-created {
    color: #{color('silver')};
    line-height: 16px;
    font-size: 13px;
    white-space: nowrap;
  }

  &-subscription-empty {
    color: #{color('silver')};
    padding: spacer(2) 0;
  }

  &-subscription-meta {
    color: #{color('gun-metal')};
    line-height: 16px;
    font-size: 13px;
  }

  &-subscription-actions {
    display: flex;
    margin-top: spacer(0.5);

    #{$root}-btn {
      line-height: 24px;
      margin-right: spacer(2);
    }
  }

  &-subscription-summary {
    &--hide {
      display: none;
    }

    &--higher {
      #{$root}-subscription-summary-description--higher {
        display: block;
      }
    }

    &--lower {
      #{$root}-subscription-summary-description--lower {
        display: block;
      }
    }
  }

  &-subscription-summary-title {
    line-height: 24px;
    font-size: 15px;
    font-weight: bold;
  }

  &-subscription-summary-description {
    display: none;
    line-height: 24px;
    font-size: 15px;
  }

  &-no-subscription {
    color: #{color('silver')};
    font-size: 15px;
    line-height: 24px;
  }
}
