// =============================================================================
// Manage subscription - Credit/debit card
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-credit-cards {
    margin-bottom: spacer(4);
  }

  &-credit-card {
    display: flex;
    align-items: center;
    border-top: 1px solid color('x11-gray');
    padding: spacer(2) 0;

    &:last-of-type {
      border-bottom: 1px solid color('x11-gray');
      margin-bottom: spacer(2);
    }
  }

  &-credit-card-logo {
    max-width: 40px;
    margin-right: spacer(2);

    img {
      display: block;
      height: auto;
      max-width: 100%;
    }
  }

  &-credit-card-data {
    display: flex;
    width: 100%;
    align-items: center;

    #{$root}-credit-card-expires {
      text-align: right;
      margin-left: auto;
    }

    @include bp-down(xs) {
      align-items: baseline;
      flex-direction: column;

      #{$root}-credit-card-expires {
        text-align: left;
        margin-left: 0;

        br {
          content: ' ';

          &:after {
            content: ' '
          }
        }
      }
    }
  }

  &-credit-card-name {
    font-size: 15px;
    font-weight: bold;
  }

  &-credit-card-expires {
    color: #{color('silver')};
    line-height: 16px;
    font-size: 13px;
  }
}
