// =============================================================================
// Manage subscription - Forms
// =============================================================================

.#{$wpfs} {
  $root: &;

  &-form-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: spacer(2);
  }

  &-form-subtitle {
    font-size: 20px;
    font-weight: bold;
    margin-top: spacer(4);
    margin-bottom: spacer(2);
  }

  &-form-lead {
    margin-bottom: spacer(5);
  }

  &-form-description {
    color: #{color('gun-metal')};
    line-height: 24px;
    font-size: 15px;
    margin-bottom: spacer(1);

    &--sm {
      line-height: 16px;
      font-size: 13px;
    }

    a {
      color: var(--link-font-color);
      transition: all .3s ease;
      border-bottom: 1px solid transparent;
      text-decoration: none;

      &:hover,
      &:active {
        color: var(--link-hover-font-color);
        border-bottom-color: var(--link-hover-font-color);
      }
    }
  }
}
